/*! modern-normalize v1.1.0 | MIT License | https://github.com/sindresorhus/modern-normalize */
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted buttontext;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-title-color: #fff;
  --description-header-hero: #ffffffb3;
  --second-title-color: #6b6b6b;
  --second-main-text-color: #1a1a1a;
  --main-red-color: #e53e44;
  --background-origin: #fff;
  --background-footer: #1a1a1a;
  --background-pages: #f0f0f0;
}

body {
  font-family: Raleway, Inter, sans-serif;
}

p, h1, h2, h3, h4, h5, h6, ul, input {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

.visually-hidden {
  white-space: nowrap;
  clip-path: inset(50%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 3.7%;
  position: relative;
}

@media screen and (width >= 1280px) {
  .container {
    padding: 0 6.7%;
  }
}

.test {
  font-family: Raleway, sans-serif;
}

.section {
  padding-top: 30px;
  padding-bottom: 30px;
}

@media screen and (width >= 768px) {
  .section {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

@media screen and (width >= 1280px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.header {
  width: 100%;
  padding-top: 70px;
  padding-bottom: 60px;
}

.home-header {
  background-image: none;
  position: absolute;
}

.other-page-header {
  background-color: #000000b3;
  background-image: linear-gradient(#000000b3, #000000b3), url("Hero.63d52fec.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 70px;
  position: relative;
}

.header__container {
  align-items: center;
}

.logo__container {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.logo__link {
  color: var(--main-title-color);
  text-align: center;
  text-decoration: none;
  display: block;
}

.logo {
  margin: 0;
  padding: 0;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.2;
}

.logo__text {
  color: var(--description-header-hero);
  font-size: 16px;
  font-weight: 400;
}

.contact__tell {
  color: var(--main-title-color);
  text-align: center;
  margin: 0 0 9px;
  padding: 0;
  font-size: 22px;
  font-weight: 500;
  line-height: 1.2;
  display: block;
}

.contact__time {
  color: var(--description-header-hero);
  text-align: center;
  margin: 0;
  padding: 0;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.2;
  display: block;
}

.header-menu__open {
  background-color: var(--main-red-color);
  border: none;
  border-bottom-left-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.burger {
  z-index: 1000;
  background-color: #f0f0f0;
  width: 280px;
  height: 100vh;
  padding: 50px 30px;
  transition: right .3s;
  position: fixed;
  top: 0;
  right: -280px;
  overflow-y: auto;
}

.burger.open {
  right: 0;
}

.header-menu__close {
  background-color: #0000;
  border: none;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  right: 0;
}

.site-nav__item-container {
  align-items: center;
  display: flex;
}

.site-nav__link {
  color: var(--second-main-text-color);
  width: 100%;
  padding: 15px 0;
  font-size: 18px;
  font-weight: 600;
  display: block;
}

.site-nav__link:hover {
  color: var(--main-red-color);
  transition: all .3s;
}

.site-nav__btn {
  background-color: #0000;
  border: none;
  width: 27px;
  height: 27px;
}

.site-nav__dropdown {
  width: 100%;
  max-height: 0;
  transition: max-height .3s ease-out, opacity .3s ease-out;
  overflow: hidden;
}

.site-nav__dropdown.open {
  max-height: 200px;
}

.site-nav__item-services {
  padding: 0 0 0 10px;
}

.site-nav__link-services {
  color: var(--second-title-color);
  padding: 15px 20px;
  font-size: 16px;
  font-weight: 500;
  display: block;
}

.site-nav__link-services:hover {
  background-color: var(--background-origin);
  color: var(--main-red-color);
}

.hero {
  background-color: #000000b3;
  background-image: linear-gradient(#000000b3, #000000b3), url("Hero.63d52fec.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  padding-bottom: 50px;
}

.hero__content {
  padding: 250px 5% 0;
}

.hero__title {
  color: var(--main-title-color);
  text-align: center;
  margin-bottom: 10px;
  font-size: 30px;
  font-weight: 600;
  line-height: 36px;
}

.hero__text {
  color: var(--description-header-hero);
  text-align: center;
  margin-bottom: 26px;
  font-size: 15px;
  line-height: 22.5px;
}

.hero__btn {
  background-color: var(--main-red-color);
  color: var(--main-title-color);
  text-align: center;
  border: none;
  border-radius: 260px;
  justify-content: center;
  width: 100%;
  margin-bottom: 30px;
  padding: 28px 0;
  font-size: 16px;
  font-weight: 600;
  display: flex;
}

.hero__btn:hover {
  box-shadow: var(--main-red-color) 0 0 0 2px inset;
  background-color: #0000;
  transition: all .3s;
}

.hero__nav {
  grid-row-gap: 10px;
  grid-template-columns: 1fr;
  padding: 0;
  list-style: none;
  display: grid;
}

.hero__nav-item {
  align-items: center;
  display: flex;
}

.hero__nav-circle {
  background-color: var(--main-title-color);
  border-radius: 50%;
  width: 12px;
  height: 12px;
}

.hero__nav-line {
  background-color: var(--main-title-color);
  border-radius: 50%;
  width: 6%;
  height: 1px;
}

.hero__nav-link {
  color: var(--main-title-color);
  border-radius: 16px;
  align-items: center;
  width: 100%;
  height: 75.5px;
  padding-left: 24px;
  font-size: 22px;
  font-weight: 600;
  line-height: 27.5px;
  text-decoration: none;
  display: flex;
  box-shadow: inset 0 0 0 1px #fff6;
}

.hero__nav-link:hover {
  background-color: var(--main-title-color);
  color: var(--second-main-text-color);
  transition: all .3s;
}

.breadcrumb {
  font-family: Inter, sans-serif;
  font-size: 15px;
}

.breadcrumb ul {
  color: var(--second-title-color);
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.breadcrumb li:not(:last-child):after {
  content: "/";
  margin: 0 .25rem;
  display: inline-block;
}

.breadcrumb li:not(:last-child) > a {
  color: #008efc;
  text-decoration: underline;
}

.breadcrumb li:not(:last-child) > a:hover, .breadcrumb li:not(:last-child) > a:focus {
  text-decoration: none;
}

.breadcrumb li:last-child > a {
  color: var(--second-title-color);
  pointer-events: none;
  cursor: default;
}

.footer {
  background-color: var(--background-footer);
  text-align: center;
  color: var(--main-title-color);
  font-family: Inter, sans-serif;
}

.footer__content {
  padding-top: 40px;
  padding-bottom: 40px;
}

@media screen and (width >= 1280px) {
  .footer__content {
    justify-content: space-between;
    display: flex;
  }
}

.footer__tel__text {
  color: var(--second-title-color);
  margin-bottom: 12px;
  font-size: 15px;
}

.footer__tel {
  color: var(--main-title-color);
  font-size: 20px;
}

@media screen and (width >= 480px) {
  .footer__tel {
    font-size: 22px;
  }
}

@media screen and (width >= 768px) {
  .footer__tel {
    font-size: 24px;
  }
}

@media screen and (width >= 1280px) {
  .footer__tel {
    font-size: 2vw;
  }
}

.footer__mail {
  color: #008efc;
  font-size: 20px;
  text-decoration: underline;
}

@media screen and (width >= 480px) {
  .footer__mail {
    font-size: 22px;
  }
}

@media screen and (width >= 768px) {
  .footer__mail {
    font-size: 24px;
  }
}

@media screen and (width >= 1280px) {
  .footer__mail {
    font-size: 2vw;
  }
}

.footer__email, .footer .contacts {
  margin-bottom: 24px;
}

@media screen and (width >= 768px) {
  .footer__email, .footer .contacts {
    margin-bottom: 30px;
  }
}

.footer__email__title {
  color: var(--second-title-color);
  margin-bottom: 9px;
}

.footer__working__text {
  font-size: 16px;
}

@media screen and (width >= 768px) {
  .footer__working__text {
    font-size: 18px;
  }
}

.footer__working__title {
  color: var(--second-title-color);
  margin-bottom: 10px;
}

.footer__copyright {
  border-top: 1px solid #ffffff26;
  padding-top: 40px;
  padding-bottom: 40px;
}

@media screen and (width >= 1280px) {
  .footer__copyright {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

.footer__copyright__text {
  font-size: 16px;
  font-weight: 500;
}

.company__title {
  margin-bottom: 24px;
  font-weight: 700;
}

@media screen and (width >= 768px) {
  .company__title {
    margin-bottom: 30px;
  }
}

@media screen and (width >= 1280px) {
  .company__title {
    text-align: start;
  }
}

.company__name {
  margin-bottom: 10px;
  font-family: Raleway, sans-serif;
  font-size: 20px;
}

@media screen and (width >= 480px) {
  .company__name {
    font-size: 22px;
  }
}

@media screen and (width >= 768px) {
  .company__name {
    font-size: 24px;
  }
}

@media screen and (width >= 1280px) {
  .company__name {
    font-size: 25px;
  }
}

.company__discription {
  color: var(--second-title-color);
  font-size: 16px;
}

.scroll_up {
  border: 1px solid var(--second-title-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  margin-top: 20px;
  padding: 17px 17px 18px;
  display: flex;
}

@media screen and (width >= 1280px) {
  .scroll_up {
    width: 62px;
    height: 62px;
    margin-top: 0;
    margin-left: 20px;
  }
}

.scroll_up:hover, .scroll_up:focus {
  border-color: var(--main-red-color);
  transition: all .3s;
}

.scroll_up__icon {
  fill: var(--main-title-color);
  width: 18px;
  height: 10px;
}

.dev__support {
  text-align: start;
  flex-direction: column;
  align-items: center;
  display: flex;
}

@media screen and (width >= 1280px) {
  .dev__support {
    flex-direction: row;
  }
}

.dev__support__text {
  margin-top: 10px;
  font-size: 14px;
}

@media screen and (width >= 1280px) {
  .dev__support__text {
    width: 260px;
    margin-top: 0;
  }
}

.testimonials {
  color: var(--second-title-color);
  font-family: Inter sans-serif;
}

.testimonials__title {
  color: var(--second-main-text-color);
  margin: 20px 0 5px;
  font-family: Raleway, sans-serif;
  font-size: 27px;
  font-weight: 600;
}

@media screen and (width >= 480px) {
  .testimonials__title {
    margin: 20px 0 10px;
    font-size: 30px;
  }
}

@media screen and (width >= 768px) {
  .testimonials__title {
    margin: 30px 0 20px;
    font-size: 40px;
  }
}

@media screen and (width >= 1280px) {
  .testimonials__title {
    margin: 40px 0 30px;
    font-size: 3.6vw;
  }
}

.testimonials__wrapper {
  grid-template-columns: 1fr;
  padding: 24px 0;
  display: grid;
}

@media screen and (width >= 768px) {
  .testimonials__wrapper {
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: minmax(270px auto);
    gap: 40px 48px;
  }
}

@media screen and (width >= 1280px) {
  .testimonials__wrapper {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.testimonials__item:not(:last-child) {
  margin-bottom: 24px;
}

.testimonials__item:last-child {
  margin-bottom: 72px;
}

@media screen and (width >= 1280px) {
  .testimonials__item:last-child {
    margin-bottom: 96px;
  }
}

.testimonials__text {
  margin-bottom: 8px;
}

.testimonials__user {
  align-items: center;
  display: flex;
}

.user__name {
  margin-bottom: 12px;
  font-family: Raleway sans-serif;
  font-size: 16px;
  font-weight: 600;
}

.user__icon {
  border-radius: 50%;
  width: 64px;
  height: 64px;
  margin-right: 10px;
  overflow: hidden;
}

@media screen and (width >= 768px) {
  .user__icon {
    width: 80px;
    height: 80px;
  }
}

.modal--second {
  z-index: 1;
  background-color: #0006;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal__content--second {
  text-align: center;
  background-color: #fff;
  border: 1px solid #888;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  width: 90%;
  margin: 15% auto;
  padding: 60px 30px 40px;
  display: flex;
  position: relative;
}

@media (width: 767px) {
  .modal__content--second {
    width: 380px;
    padding: 60px 32px 40px;
  }
}

@media (width <= 480px) {
  .modal__content--second {
    width: 280px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.modal__close--second {
  text-align: center;
  fill: var(--second-title-color);
  width: 64px;
  height: 64px;
  padding: 10px;
  font-weight: bold;
  position: absolute;
  top: 0;
  right: 0;
}

.modal__close--second:hover, .modal__close--second:focus {
  cursor: pointer;
  text-decoration: none;
}

.modal__header {
  font-size: 22px;
  font-weight: 600;
  line-height: 120%;
}

@media (width <= 479px) {
  .modal__subheader {
    color: var(--second-title-color);
    margin-top: 8px;
    font-size: 15px;
  }
}

.modal__form label {
  text-align: left;
  margin-bottom: 5px;
  display: block;
}

.modal__form input[type="text"], .modal__form input[type="tel"] {
  background-color: var(--background-pages);
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  height: 62px;
  margin-bottom: 10px;
  padding: 10px;
}

.modal__form input[type="checkbox"] {
  margin-right: 5px;
}

.modal__form button {
  background-color: var(--main-red-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 250px;
  width: 100%;
  min-height: 82px;
  margin-top: 15px;
  padding: 20px 30px;
  font-family: Raleway, sans-serif;
  font-size: 16px;
  font-weight: 600;
}

@media screen and (width <= 768px) {
  .modal__form button {
    margin-top: 25px;
    font-size: 17px;
  }
}

@media screen and (width <= 480px) {
  .modal__form button {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.modal__form button:hover {
  background-color: var(--main-title-color);
  color: #000;
  border: 2px solid var(--main-red-color);
  transition: all .3s;
}

.modal--hero {
  z-index: 1;
  background-color: #0006;
  width: 100%;
  height: 100%;
  padding: 16px;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal__content--hero {
  background-color: #fff;
  border: 1px solid #888;
  border-radius: 19px;
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 16px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
  box-shadow: 0 16px 32px #00000014;
}

.modal__left--hero {
  color: #fff;
  background-image: url("firstModalLeftImage.4f458640.png");
  background-position: center;
  background-size: cover;
  border-radius: 19px;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  display: flex;
}

.modal__left--hero h2 {
  margin: 0;
  font-size: 24px;
}

.modal__left--hero p {
  margin: 10px 0 0;
  font-size: 16px;
}

.modal__right--hero {
  background-color: #fff;
  border-radius: 19px;
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 30px;
  font-family: Raleway, sans-serif;
  display: flex;
  position: relative;
  overflow: hidden;
}

.modal__right--hero h3 {
  color: #000;
  padding-bottom: 24px;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.modal__form--hero {
  width: 100%;
}

.modal__form--hero label {
  color: #222;
  margin-bottom: 4px;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  display: block;
  position: relative;
}

.modal__form-item--hero {
  flex-direction: column;
  margin-bottom: 16px;
  padding: 0;
  display: flex;
}

.modal__form--hero input[type="text"], .modal__form--hero input[type="tel"], .modal__form--hero input[type="url"] {
  letter-spacing: -.25px;
  background: #fff;
  border: 1px solid #d0d5dd;
  border-radius: 104px;
  outline: none;
  width: 100%;
  height: 48px;
  padding: 0 54px 0 24px;
  font-size: 14px;
  font-weight: 400;
  line-height: 36px;
}

.input-wrapper {
  position: relative;
}

.input-wrapper .icon {
  pointer-events: none;
  position: absolute;
  top: 50%;
  right: 24px;
  transform: translateY(-50%);
}

input#business:focus + .icon path, input#competitors:focus + .icon path, input#name:focus + .icon path, input#phone:focus + .icon path {
  stroke: #c920e4;
}

.modal__form--hero input[type="text"]:focus, .modal__form--hero input[type="tel"]:focus, .modal__form--hero input[type="url"]:focus {
  border-color: #c920e4;
}

.modal__form--hero button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background: linear-gradient(90deg, #582ed2 0%, #c91fe3 100%), #6532f7;
  border: none;
  border-radius: 108px;
  outline: 0;
  width: 100%;
  max-width: 100%;
  height: 56px;
  margin-top: 32px;
  padding: 0 5px;
  font-size: 15px;
  font-weight: 600;
  line-height: 56px;
  display: block;
}

.modal__close--hero {
  cursor: pointer;
  z-index: 10;
  background-color: #fff6;
  background-image: url("closeModal-min.34de0477.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 36px;
  height: 36px;
  transition: all .2s ease-in-out;
  display: inline-block;
  position: absolute;
  top: 16px;
  right: 15px;
}

.modal__close--hero:hover, .modal__close--hero:focus {
  color: #000;
  text-decoration: none;
}

@media (width <= 900px) {
  .modal__content--hero {
    flex-direction: column;
  }

  .modal__left--hero {
    display: none;
  }

  .modal__right--hero {
    flex: 1;
    padding: 20px;
  }
}

.we-work__title {
  color: var(--second-main-text-color);
  margin-bottom: 10px;
  font-family: Raleway, sans-serif;
  font-size: 27px;
  font-weight: 600;
}

@media screen and (width >= 480px) {
  .we-work__title {
    font-size: 30px;
  }
}

@media screen and (width >= 768px) {
  .we-work__title {
    margin-bottom: 12px;
    font-size: 40px;
  }
}

@media screen and (width >= 1280px) {
  .we-work__title {
    margin-bottom: 2.05%;
    font-size: 3.6vw;
  }
}

.we-work__description {
  color: var(--second-title-color);
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 1.5;
}

@media screen and (width >= 768px) {
  .we-work__description {
    margin-bottom: 45px;
    font-size: 18px;
  }
}

@media screen and (width >= 1280px) {
  .we-work__description {
    margin-bottom: 50px;
    font-size: 20px;
    line-height: 1.6;
  }
}

.slider__wrapper {
  margin: 0 -10px;
  padding-bottom: 40px;
  display: flex;
  overflow: hidden;
}

.slider__item {
  border: 2px solid #ddd;
  border-radius: 10px;
  width: 100%;
  height: 100px;
  padding: 20px;
}

.slider__item:not(:last-child) {
  margin-right: 10px;
}

.slider__card {
  justify-content: center;
  align-items: center;
  display: flex;
}

.slider__icon {
  border: 1px solid var(--second-title-color);
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-right: 25px;
}

.slider__title {
  color: var(--second-main-text-color);
  font-size: 22px;
  font-weight: 700;
}
